#contact {
  width: 100%;
  min-height: 100vh;
  background-color: #f5f8fc;
  place-items: center;
  padding-bottom: 5em;
  display: grid;
}

#contact .container {
  width: 90%;
  height: 90%;
  flex-flow: column;
  display: flex;
}

#contact .container .text-area {
  width: 100%;
}

#contact .container .text-area h3 {
  color: #1a1a1a;
  font-size: 3.625rem;
  font-weight: 700;
}

#contact .container .text-area h4 {
  margin-top: 2.5em;
  font-size: 1.5rem;
  font-weight: 400;
}

#contact .container .text-area small {
  font-size: 1.3125rem;
  font-weight: 400;
  line-height: 1.38em;
}

#contact .container .cards {
  flex-flow: column wrap;
  justify-content: space-around;
  align-items: center;
  gap: 1.8em;
  margin-top: 2.8em;
  margin-bottom: 2em;
  display: flex;
}

#contact .container .cards .card {
  width: 95%;
  height: 10.6588em;
  background-color: #fff;
  border-radius: .2em;
  flex-flow: column wrap;
  transition: all .3s;
  display: flex;
  box-shadow: 0 1px 4px #00000029;
}

#contact .container .cards .card .card-item {
  color: #1a1a1a;
}

#contact .container .cards .card .card-item h3 {
  font-size: 1.5rem;
  font-weight: 400;
}

#contact .container .cards .card .card-item a {
  text-decoration: underline;
}

#contact .container .cards .card .card-item iframe {
  width: 100%;
  height: 100%;
}

#contact .container .cards .card .card-item-text {
  width: 100%;
  height: 100%;
  place-items: center;
  display: grid;
}

#contact .container .cards .card-dark:hover {
  filter: invert();
}

@media (min-width: 960px) {
  #contact .container .cards {
    flex-flow: row;
  }
}

#contact .container .card-form {
  text-align: center;
  background-color: #fff;
  border-radius: .2em;
  flex-flow: wrap;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 1em;
  padding: 2em;
  transition: all .3s;
  display: flex;
  box-shadow: 0 1px 4px #00000029;
}

#contact .container .card-form h5 {
  font-size: 1.5rem;
  font-weight: 400;
}

#contact .container .card-form h6 {
  color: #1a1a1a;
  margin-top: 1.4em;
  font-size: 1.625rem;
  font-weight: 700;
  line-height: 1.38em;
}

#contact .container .card-form form {
  text-align: left;
  margin-top: 2em;
  display: grid;
}

#contact .container .card-form form input {
  border-bottom: 1px solid #1a1a1a;
  font-size: 1.3125rem;
  font-weight: 400;
  line-height: 1.38em;
  overflow: auto;
}

#contact .container .card-form form textarea {
  resize: none;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #1a1a1a;
  outline: none;
  margin-top: 1.3em;
  font-size: 1.3125rem;
  font-weight: 400;
  line-height: 1.38em;
}

#contact .container .card-form form .contact-message-error {
  color: #e62424;
  transition: all .3s;
  display: none;
}

#contact .container .card-form form .contact-message-sucess {
  color: #37d637;
  transition: all .3s;
  display: none;
}

#contact .container .card-form form button {
  background-color: #fff;
  border: 1px solid #1a1a1a;
  border-radius: .2em;
  outline: none;
  margin-top: 1.3em;
  padding: 1em;
  transition: all .3s;
}

#contact .container .card-form form button:hover {
  filter: invert();
}

